/* Global Styles */
body {
  background-color: #f3f4f6;
  color: #333;
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

/* Navbar */
nav {
  background-color: #2563eb;
  color: white;
  padding: 15px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

nav a {
  color: white;
  text-decoration: none;
  margin: 0 10px;
  font-weight: bold;
}

nav a:hover {
  text-decoration: underline;
}

/* Container */
.container {
  max-width: 600px;
  margin: 20px auto;
  padding: 20px;
}

/* Page Titles */
h1, h2 {
  text-align: center;
  font-size: 24px;
  margin-bottom: 15px;
}

/* Form Styles */
form {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
  max-width: 400px;
  margin: 20px auto;
}

input {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

input:focus {
  outline: none;
  border-color: #2563eb;
}

/* Buttons */
button {
  width: 100%;
  padding: 10px;
  color: white;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s;
}

button:hover {
  opacity: 0.9;
}

/* Login Button */
button.login {
  background-color: #2563eb;
}

/* Signup Button */
button.signup {
  background-color: #16a34a;
}
